<template>
  <div class="qs-list">
    <ul v-if="qsList.length == 0 ? false : true">
      <li></li>
      <li>标题</li>
      <li>截止时间</li>
      <li>状态</li>
      <li>操作<span @click="say('您没有权限操作')">+ 新建测评</span></li>
      <!-- <li>操作<span style="color:#BEBEBE" @click="$router.push({path: '/'})">+ 新建测评</span></li> -->
      <!-- <li>操作<span style="color:#BEBEBE" @click="$router.push({name: 'qsEdit', params: {num: 0}})">+ 新建测评</span></li> -->
    </ul>
    <template v-for="item in qsList">
      <ul>
        <li><input type="checkbox" v-model="item.checked"></li>
        <li>{{item.title}}</li>
        <li>{{item.time}}</li>
        <li :class="item.state === 'inissue' ? 'inissue' : ''">{{item.stateTitle}}</li>
        <li>
          <button style="color:#BEBEBE" @click="iterator = edit(item); iterator.next()">编辑</button>
          <button style="color:#BEBEBE" @click="iterator = delItem(item.num); iterator.next()">删除</button>
          <router-link :to="`/fill/${item.num}`" tag="button">开始测评</router-link>
          <button @click="iterator = watchData(item); iterator.next()">统计数据</button>
        </li>
      </ul>
    </template>
    <div class="list-bottom" v-if="qsList.length == 0 ? false : true">
      <label><input type="checkbox" id="all-check" v-model="selectAll">全选</label>
      <button style="color:#BEBEBE" @click="iterator = delItems(); iterator.next()">删除</button>
    </div>
      <div class="add-qs" v-if="qsList.length === 0">
        <button class="add-btn" @click="$router.push({name: 'qsEdit', params: {num: 0}})">+&nbsp;&nbsp;新建测评</button>
      </div>
    <div class="shadow" v-if="showDialog">
      <div class="del-dialog">
        <header>
          <span>提示</span>
          <span class="close-btn" @click="showDialog = false">X</span>
        </header>
        <p>{{info}}</p>
