              {'num': 'Q157', 'title': '我宁愿服饰素洁大方，而不愿争其斗艳惹人注目', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 不太确定',1],['C 不是的',2]],'values':'F'},
              {'num': 'Q158', 'title': '黄昏时安静的娱乐活动远胜过热闹的宴会', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 不太确定',1],['C 不是的',2]],'values':'F'},
              {'num': 'Q159', 'title': '我常常明知故犯，不愿意接受好心的建议', 'type': 'radio', 'isNeed': true, 'options': [['A 偶然如此',0],['B 罕有如此',1],['C 从不如此',2]],'values':'G'},
              {'num': 'Q160', 'title': '我总把[是非][善恶]作为判断或取舍的言责：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'G'},
              {'num': 'Q161', 'title': '我工作时不喜欢有许多人在场参观：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 介于A与C之间',1],['C 不是的',2]],'values':'H'},
              {'num': 'Q162', 'title': '我故意为难一些有教养的人，如医生教师，是一件有趣的事：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 介于A与C之间',1],['C 不是的',2]],'values':'I'},
              {'num': 'Q163', 'title': '在各种课程中，我较喜欢', 'type': 'radio', 'isNeed': true, 'options': [['A 语文',2],['B 不确定',1],['C 数学',0]],'values':'I'},
              {'num': 'Q164', 'title': '那些自以为是，道貌岸然的人最使我生气：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'L'},
              {'num': 'Q165', 'title': '与平常循规蹈矩的人交谈：', 'type': 'radio', 'isNeed': true, 'options': [['A 颇有兴趣，亦有所得',0],['B 介于A与C之间',1],['C 他们思想肤浅使我厌烦',2]],'values':'M'},
              {'num': 'Q166', 'title': '我喜欢：', 'type': 'radio', 'isNeed': true, 'options': [['A 有几个平时对我很苛求，而确富有感情的朋友',0],['B 介于A与C之间',1],['C 不受别人的牵涉',2]],'values':'M'},
              {'num': 'Q167', 'title': '如果作民意调查时，我宁愿投票赞同：', 'type': 'radio', 'isNeed': true, 'options': [['A 切实根绝有心理缺陷者的生育',2],['B 不确定',1],['C 对杀人犯判处死刑',0]],'values':'N'},
              {'num': 'Q168', 'title': '我有时会无端地感到沮丧痛苦：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'O'},
              {'num': 'Q169', 'title': '当我与立场相反的人辩论时，我主张：', 'type': 'radio', 'isNeed': true, 'options': [['A 尽量找出基本观点的差异',2],['B 不一定',1],['C 彼此让步以解决矛盾',0]],'values':'Q1'},
              {'num': 'Q170', 'title': '我一向重感情而不重理智，因此我的观点常常动摇不定：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 大致如此',1],['C 不是的',2]],'values':'Q1'},
              {'num': 'Q171', 'title': '我的学习效率多有赖于', 'type': 'radio', 'isNeed': true, 'options': [['A 阅读',2],['B 介于A与C之间',1],['C 参加团体讨论',0]],'values':'Q2'},
              {'num': 'Q172', 'title': '我宁愿一个高薪的工作，不在乎有无保障，而不愿任低薪的固定工作：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 不太确定',1],['C 不是的',2]],'values':'Q3'},
              {'num': 'Q173', 'title': '在参加辩论以前，我总先以为把握主自己的立场：', 'type': 'radio', 'isNeed': true, 'options': [['A 经常如此',2],['B 一般如此',1],['C 必要事如此',0]],'values':'Q3'},
              {'num': 'Q174', 'title': '我常被一些无谓的琐事烦扰：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'Q4'},
              {'num': 'Q175', 'title': '我宁愿住在嘈杂的城市，而不愿住在安静的乡村：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 不太确定',1],['C 不是的',2]],'values':'Q4'},
              {'num': 'Q176', 'title': '我宁愿：', 'type': 'radio', 'isNeed': true, 'options': [['A 负责领导儿童游戏',2],['B 不确定',1],['C 协助钟表修理',0]],'values':'A'},
              {'num': 'Q177', 'title': '一人一事，众人受累，我对这句话的反应是 ：', 'type': 'radio', 'isNeed': true, 'options': [['A 愤',1],['B 偾',0],['C 喷',0]],'values':'B'},
              {'num': 'Q178', 'title': '望子成龙的家长，往往（）苗助长', 'type': 'radio', 'isNeed': true, 'options': [['A 揠',1],['B 堰',0],['C 偃',0]],'values':'B'},
              {'num': 'Q179', 'title': '气候的转变并不影响我的情绪：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'C'},
              {'num': 'Q180', 'title': '因为我对于一切问题都有些见解，大家都公认我富于思想：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'E'},
              {'num': 'Q181', 'title': '我讲话的声音', 'type': 'radio', 'isNeed': true, 'options': [['A 洪亮',2],['B 介于A与C之间',1],['C 低沉',0]],'values':'E'},
              {'num': 'Q182', 'title': '人们公认我是一个活跃热情的人：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'F'},
              {'num': 'Q183', 'title': '我喜欢有旅行和变动机会的工作，而不计较工作本身之是否有保障', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'F'},
              {'num': 'Q184', 'title': '我治事严格，凡是都务求正确尽善：', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'G'},
              {'num': 'Q185', 'title': '在取回或归还东西时，我总仔细检查东西是否还保持原状', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 介于A与C之间',1],['C 不是的',0]],'values':'G'},
              {'num': 'Q186', 'title': '我通常精力充沛', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',2],['B 不一定',1],['C 不是的',0]],'values':'H'},
              {'num': 'Q187', 'title': '我确信我没有遗漏或不经心回答上面任何问题', 'type': 'radio', 'isNeed': true, 'options': [['A 是的',0],['B 不确定',0],['C 不是的',0]],'values':''}




         
            ]
          },

          { 'num': 3,
            'title': '心理健康症状自评量表SCL-90', 
            'time': '2025-5-12', 
            'state': 'inissue', 
            'stateTitle': '发布中', 
            'checked': false, 
            'question': [
              {'num': 'Q1', 'title': '单选题', 'type': 'radio', 'isNeed': true, 'options': ['选项一', '选项二']},
              {'num': 'Q2', 'title': '多选题', 'type': 'checkbox', 'isNeed': true, 'options': ['选项一', '选项二', '选项三', '选项四']},
              {'num': 'Q3', 'title': '文本题', 'type': 'textarea', 'isNeed': true}
            ]
          },

          { 'num': 4,
            'title': '已发布测评', 
            'time': '2017-3-27', 
            'state': 'nosueed', 
            'stateTitle': '已发布', 
            'checked': false, 
            'question': [
              {'num': 'Q1', 'title': '单选题', 'type': 'radio', 'isNeed': true, 'options': ['选项一', '选项二']},
              {'num': 'Q2', 'title': '多选题', 'type': 'checkbox', 'isNeed': true, 'options': ['选项一', '选项二', '选项三', '选项四']},
              {'num': 'Q3', 'title': '文本题', 'type': 'textarea', 'isNeed': true}
            ]
          },

          { 'num': 5,
            'title': '已发布测评', 
            'time': '2017-3-27', 
            'state': 'nosueed', 
            'stateTitle': '已发布', 
            'checked': false, 
            'question': [
              {'num': 'Q1', 'title': '单选题', 'type': 'radio', 'isNeed': true, 'options': ['选项一', '选项二']},
              {'num': 'Q2', 'title': '多选题', 'type': 'checkbox', 'isNeed': true, 'options': ['选项一', '选项二', '选项三', '选项四']},
              {'num': 'Q3', 'title': '文本题', 'type': 'textarea', 'isNeed': true}
            ]
          }

        ]);
        this.qsList = storage.get(); //从localstorage中取出值
      }
    },
    methods: {
      showDialogMsg(info) {
        this.showDialog = true;
        this.info = info;
      },
      say: function (message) {
        this.showDialogMsg(message)

    },
      *delItem(num) {
        // yield this.showDialogMsg('确认要删除此测评')
        yield this.showDialogMsg('您没有权限操作')

        // yield (() => {
        //   let index = 0;
        //   for (let length = this.qsList.length; index < length; index++) {
        //     if (this.qsList[index].num === num) break;
        //   }
        //   this.qsList.splice(index, 1);
        //   this.showDialog = false;
        // })();
      },
      *delItems() {
        yield this.showDialogMsg('您没有权限操作');
        // yield this.showDialogMsg('确认要删除选中的测评？');

        // yield (() => {
        //   this.showDialog = false;
        //   if (this.selectAll) {
        //     this.qsList = [];
        //     return;
        //   }
        //   if (this.selectGroup == []) return;

        //   this.selectGroup.forEach( item => {
        //     if (this.qsList.indexOf(item) > -1) this.qsList.splice(this.qsList.indexOf(item), 1);
        //   } )
        // })();     
      },
      *edit(item) {
        yield (() => {
          if (item.state === 'noissue') {
            this.showDialogMsg('您没有权限操作？');
            // this.showDialogMsg('确认要编辑？');
          } else {
            this.showDialogMsg('您没有权限操作');
            // this.showDialogMsg('只有未发布的测评才能编辑');
          }
        })();
        yield (() => {
          if (item.state !== 'noissue') {
            this.showDialog = false;
          } else {
            this.showDialog = false;
            this.$router.push({name: 'qsEdit', params: { num: item.num }})
          }
        })();
      },
      *watchData(item) {
        yield (() => {
          if (item.state === 'noissue') {
            this.showDialogMsg('未发布的测评无数据可查看');
          } else {
            this.$router.push({ name: 'qsData', params: { num: item.num }})
          }
        })();
        yield this.showDialog = false;
      }
    },
    computed: {
      selectAll: {
        get() {
          return this.selectCount === this.qsList.length && this.selectCount !== 0;
        },
        set(value) {
          this.qsList.forEach( item => {
            item.checked = value;
          } );
          return value;
        }
      },
      selectCount() {
        let i = 0;
        this.qsList.forEach( item => {
          if (item.checked) i++;
        } );
        return i;
      },
      selectGroup() {
        let group = [];
        this.qsList.forEach( item => {
          if (item.checked) group.push(item);
        } );
        return group;
      }
    },
    watch: {
      qsList: {
        handler(val) {
          val.forEach( (item, index) => {
            item.num = index + 1
          } )
          storage.save(val);
        },
        deep: true
      }
    }
  }
</script>

<style lang="scss" scoped>
@import '../style/QS-list';
</style>


