import Vue from 'vue'
import Router from 'vue-router'
import qsList from '@/components/QS-list'
import qsData from '@/components/QS-data'
import qsFill from '@/components/QS-fill'
import qsEdit from '@/components/QS-edit'
import qsResult1 from '@/components/QS-result1'
import qsResult2 from '@/components/QS-result2'
import qsResult3 from '@/components/QS-result3'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'qsList',
      component: qsList
    },
    {
      path: '/data/:num',
      name: 'qsData',
      component: qsData
    },
    {
      path: '/fill/:num',
      name: 'qsFill',
      component: qsFill
    },
    {
      path: '/edit/:num',
      name: 'qsEdit',
      component: qsEdit
    },
    {
      path:'/result1',
      name:'qsResult1',
      component:qsResult1
    },
        {
      path:'/result2',
      name:'qsResult2',
      component:qsResult2
    },
        {
      path:'/result3',
      name:'qsResult3',
      component:qsResult3
    }
  ]
})



