<template>
  <div class="fill-container">
    <div class="fill" v-if="!isError">
      <router-link to="/" tag="span" class="back">&lt; 返回</router-link>
      <h2>{{qsItem.title}}</h2>
      <div class="content-header">
      </br>
      <h3>指导语：</h3>
      <p>本测验由许多与你有关的问题组成。当你阅读每一题目时，请考虑是否符合你自己的实际情况和看法。</p>
      <p>请尽快填写你看完题目后的第一印象，不要在每一道题目上费太多时间思索。答案无所谓对与不对，好与不好，完全不必有任何顾虑。</p>
    </div>
      <div class="content">
        <div class="content-item" v-for="item in qsItem.question">
          <p class="qs-title" :name="`${item.values}`">
            {{item.num}}&nbsp;{{item.title}}&nbsp;{{getMsg(item)}}
          </p>
          <p v-for="(option,index) in item.options" class="option">
            <label>
              <input 
              type="radio" 
              :name="`${item.num}-${item.title}`"
              v-model="requiredItem[item.num]"
              v-if="item.type === 'radio'"
              :value="`${option[1]}`"
              :class="`${item.values}`">
              <input 
              type="checkbox" 
              :name="`${item.num}-${item.title}`"
              v-model="requiredItem[item.num]"
              v-if="item.type === 'checkbox'"
              :value="option">{{option[0]}}
            </label>
          </p>
          <textarea 
          v-if="item.type === 'textarea'" 
          v-model="requiredItem[item.num]"></textarea>
        </div>
      </div>
      <transition name="fade">
        <div class="dialog" v-if="showDialog">
          <div class="submit-dialog" v-if="submitError">
            <header>
              <span>提示</span>
              <span class="close-btn" @click="showDialog = false">X</span>
            </header>
            <p>{{info}}</p>
            <div class="btn-box">
              <button class="yes" @click="showDialog = false">确定</button>
              <button @click="showDialog = false">取消</button>
            </div>
          </div>
          <div class="submit-dialog" v-else>
            <header>
              <span>提示</span>
              <span class="close-btn" @click="showDialog = false">X</span>
            </header>
            <p>{{info}}</p>
          </div>
        </div>
      </transition>  
      <footer>
        <button @click="submit" class="submit">提交</button>
      </footer>
    </div>
    <div class="error" v-else>
      404 Not Found
    </div>
  </div>
</template>

<script>
import storage from '../store.js'
import jquery from 'jquery'

/**
 * A module that define qs-fill router view
 * @exports qs-fill
 * @author oyh(Reusjs)
 */
  export default {
    name: 'qsFill',
    data() {
      return {
        qsItem: [],
        qsList: storage.get(),
        isError: false,
        showDialog: false,
        info: '',
        submitError: false,
        requiredItem: {}
      }
    },
    created() {
      this.fetchData()
    },
    mounted() {
      this.getRequiredItem()
    },
    methods: {
      fetchData() {
        let i = 0;
        for (let length = this.qsList.length; i < length; i++) {
          if (this.qsList[i].num == this.$route.params.num) {
            this.qsItem = this.qsList[i]
            break
          }
        }
        if (i === this.qsList.length) this.isError = true
      },
      getMsg(item) {
        let msg = ''
        if (item.type === 'radio') {
          msg = '(单选题)'
        } else if (item.type === 'checkbox') {
          msg = '(多选题)'
        } else {
          msg = '(文本题)'
        }

        return item.isNeed ? `${msg} *` : msg
      },
  
      submit() {
        if (this.qsItem.num===1) {
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          result = true  // 生产模式需要关掉
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            // this.info = '提交成功'
            var cat = {'A':'胆汁质','B':'多血质','C':'粘液质','D':'抑郁质','E':'多项混合型'}

            var countA = 0
            var countB = 0
            var countC = 0
            var countD = 0
            var arr = new Array()

            function descend(x,y){
                return y[1] - x[1];  //按照数组的第2个值降序排列
            }

            $('input.A:radio:checked').each(function(){      
                countA+=parseInt($(this).val())
                if ($(this).val()==2){
                  alert($(this).val())  
                }
                         
            })
            arr[0] = ['A',countA]
            $('input.B:radio:checked').each(function(){      
                countB+=parseInt($(this).val())            
            })
            arr[1] = ['B',countB]
            $('input.C:radio:checked').each(function(){      
                countC+=parseInt($(this).val())            
            })
            arr[2] = ['C',countC]
            $('input.D:radio:checked').each(function(){      
                countD+=parseInt($(this).val())            
            })
            arr[3] = ['D',countD]
            arr.sort(descend) //二维数组降序排序

            // 结果分析
            if (arr[0][1]-arr[1][1]>=4){
              // alert(arr[0][0])
              storage.save([{'res':cat[arr[0][0]]}],'result1')
              // this.info = '提交成功'
              
            }
            else{
              if (arr[1][1]-arr[2][1]>=4){
                // alert(arr[0][0]+arr[1][0])
                storage.save([{'res':cat[arr[0][0]]+cat[arr[1][0]]}],'result1')
                // this.info = '提交成功'
              }
              else{
                // alert('多项混合质')
                storage.save([{'res':cat['E']}],'result1')
                // this.info = '提交成功'
              }
              }
            
            this.info = '提交成功'
            setTimeout(() => {
              this.showDialog = false
            }, 1000)
            setTimeout(() => {
              this.$router.push({path: '/result1'})
            }, 1200)
          } else {
            this.showDialog = true
            this.submitError = true
            this.info = '提交失败！ 存在必填项未填'
          }
        } else {
          this.showDialog = true
          this.submitError = true
          this.info = '提交失败！ 只有发布中的问卷才能提交'
        }
      }

      // 问卷2
      if (this.qsItem.num===2){
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          result = true  // 生产模式需要关掉
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            // this.info='问卷2'

            var A = 0
            var B = 0
            var C = 0
            var E = 0
            var F = 0
            var G = 0 
            var H = 0
            var I = 0
            var L = 0
            // storage.save([{'L':l}],'fd')
            var M = 0
            var N = 0
            var O = 0
            var Q1 = 0
            var Q2 = 0
            var Q3 = 0
            var Q4 = 0

            // 因素A
            $('input.A:radio:checked').each(function(){      
                A+=parseInt($(this).val())
                // if ($(this).val()==2){
                //   alert($(this).val())  
                // }
                         
            })

            $('input.B:radio:checked').each(function(){      
                B+=parseInt($(this).val())       
            })

            $('input.C:radio:checked').each(function(){      
                C+=parseInt($(this).val())       
            })

            $('input.E:radio:checked').each(function(){      
                E+=parseInt($(this).val())       
            })

            $('input.F:radio:checked').each(function(){      
                F+=parseInt($(this).val())       
            })

            $('input.G:radio:checked').each(function(){      
                G+=parseInt($(this).val())       
            })

            $('input.LL:radio:checked').each(function(){      
                L+=parseInt($(this).val()) 
                alert(L)
                // l = 9
                // storage.save([{'L':l}],'fd')
            })

            $('input.H:radio:checked').each(function(){      
                H+=parseInt($(this).val())       
            })

            $('input.I:radio:checked').each(function(){      
                I+=parseInt($(this).val())       
            })

            $('input.M:radio:checked').each(function(){  
                M+=parseInt($(this).val())
                // alert(M)    
            })

            $('input.N:radio:checked').each(function(){  
                N+=parseInt($(this).val())
                // alert(N)      
            })

            $('input.O:radio:checked').each(function(){      
                O+=parseInt($(this).val())       
            })

            $('input.Q1:radio:checked').each(function(){      
                Q1+=parseInt($(this).val())       
            })

            $('input.Q2:radio:checked').each(function(){      
                Q2+=parseInt($(this).val())       
            })

            $('input.Q3:radio:checked').each(function(){      
                Q3+=parseInt($(this).val())       
            })

            $('input.Q4:radio:checked').each(function(){      
                Q4+=parseInt($(this).val())       
            })

            storage.save([{'A':A},{'B':B},{'C':C},{'E':E},{'F':F},{'G':G},{'H':H},{'I':I},{'L':L},{'M':M},{'N':N},{'O':O},{'Q1':Q1},{'Q2':Q2},{'Q3':Q3},{'Q4':Q4}],'result2')
            this.info = '提交成功'
            setTimeout(() => {
              this.showDialog = false
            }, 1000)
            setTimeout(() => {
            this.$router.push({path: '/result2'})
            }, 1200)
          }
          else {
            this.showDialog = true
            this.submitError = true
            this.info = '提交失败！ 存在必填项未填'
          }
        } else {
          this.showDialog = true
          this.submitError = true
          this.info = '提交失败！ 只有发布中的问卷才能提交'
        }
      }

      // 问卷3
      if (this.qsItem.num===3){
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          result = true  // 生产模式需要关掉
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            storage.save([{'res':2}],'result3')
            this.info='问卷3'
            setTimeout(() => {
              this.showDialog = false
            }, 1000)
            setTimeout(() => {
            this.$router.push({path: '/result3'})
            }, 1200)
          }
          else {
            this.showDialog = true
            this.submitError = true
            this.info = '提交失败！ 存在必填项未填'
          }
        } else {
          this.showDialog = true
          this.submitError = true
          this.info = '提交失败！ 只有发布中的问卷才能提交'
        }
      }


      // 问卷4
      if (this.qsItem.num===4){
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          result = true  // 生产模式需要关掉
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            storage.save([{'res':2}],'result3')
            this.info='问卷3'
            setTimeout(() => {
              this.showDialog = false
            }, 1000)
            setTimeout(() => {
            this.$router.push({path: '/result3'})
            }, 1200)
          }
          else {
            this.showDialog = true
            this.submitError = true
            this.info = '提交失败！ 存在必填项未填'
          }
        } else {
          this.showDialog = true
          this.submitError = true
          this.info = '提交失败！ 只有发布中的问卷才能提交'
        }
      }

      // 问卷5
      if (this.qsItem.num===5){
        if (this.qsItem.state === 'inissue') {
          let result = this.validate()
          result = true  // 生产模式需要关掉
  
          if (result) {
            this.showDialog = true
            this.submitError = false
            this.info='问卷5'
          }
        }
      }

      },
      getRequiredItem() {
        this.qsItem.question.forEach( item => {
          if (item.isNeed) {
            if (item.isNeed) {
              if (item.type === 'checkbox') {
                this.requiredItem[item.num] = []
              } else {
                this.requiredItem[item.num] = ''
              }
            }
          }
        } )
      },
      validate() {
        for (let i in this.requiredItem) {
          if (this.requiredItem[i].length === 0) return false
        }
        return true
      }
    },
    watch: {
      '$route': 'fetchData'
    }
  }
</script>

<style lang="scss" scoped>
@import '../style/QS-fill'
</style>


